*
{
  margin: 0;
  padding: 0;
}
body{
  background: url("http://getwallpapers.com/wallpaper/full/7/a/4/233702.jpg");
  font-family: "Bree Serif",serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.heading{
  text-align: center;
  color: rgb(214, 7, 128);
}
.app
{
  width: 95%;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgb(12, 6, 6);
  display: flex;
  justify-content: space-evenly;
}
.Question-Section{
  position: relative;
  width: 100%;
  color: white;
}
.Question-Section h1 span{
  color: rgb(255, 136, 0);
  font-size: 40px;
  margin-bottom: 10px;
}
.Question-Text
{
  font-size: 30px;
  margin-top: 10px;
  color: rgb(30, 219, 30);
}
.Answer-Section
{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
}
button{
  width: 100%;
  font-size: 16px;
  border-radius: 15px;
  display: flex;
  padding: 10px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid rgb(68, 60, 60);
  cursor: pointer;
}
.score-section{
  display: flex;
  align-items: center;
  font-size: 20px;
  color: aqua;
  font-weight: bold;
}